@import "../../../../../sass/utils.scss";

.UI_body {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 999999;
  pointer-events: none;
  .container {
    position: fixed;
    top: calc(50% - (196px / 2));
    left: 16px;
    width: 2.75em;
    border-radius: 3px;
    border: 1px solid black;
    background-color: white;
    pointer-events: all;

    header {
      @include size(100%, 16px);
      background-color: $header_menu;
      content: "";
      cursor: grab;
    }

    .menu_content {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding: 0;
      margin: 0.5em 0 0.5em 0;
      li {
        display: flex;
        align-items: center;
        justify-content: center;
        @include size(2em, 2em);
        margin: 0.25rem;
        background-color: white;
        background-color: rgb(202, 202, 202);
        border-radius: $softRadius;
        text-align: center;
        color: white;
        font-weight: bold;

        img {
          @include size(1.6rem, 1.6rem);
          color: red;
        }

        &.active {
          background: white;
          border: 2px solid red;
          color: black;
        }
      }
    }
  }
}
