@import "../../../../../sass/utils.scss";

.guides {
  display: "block";
  cursor: grab;
  pointer-events: all;
  z-index: 999990;

  &:hover {
    cursor: grab;
    pointer-events: all;
  }
}

.guides_pseudoEL {
  position: absolute;
  z-index: 999990;
  height: 0;
  width: 0;
  display: block;
  content: "";
  cursor: hand;
}

.UI_guides__container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #34495e;
  position: fixed;
  padding: 0 1em;
  width: 100px;
  height: 50px;
  bottom: 0;
  right: 0;
  border-top-left-radius: 5px;

  label {
    margin: 0 1em 0 0;
    color: white;
    font-size: 0.75em;
    text-align: center;
  }

  input {
    width: 30px;
    padding: 0 8px;
    border-radius: 0;
    background: transparent;
    background-color: white;
    border: 1px solid black;
    border-radius: $softRadius;
    text-align: center;
    pointer-events: all;
  }
}

.contextMenu {
  position: fixed;
  z-index: 999998;
  border-radius: 3px;
  border: 1px solid black;
  background: white;
  cursor: grab;
  pointer-events: all;

  header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 0.25em 0.5em 0.5em;
    background: rgb(59, 69, 99);
    box-sizing: border-box;
    cursor: grab;

    label {
      margin-left: 0.5em;
      font-size: 1em;
      color: white;
      cursor: grab;
    }

    .close_contextMenu {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: space-between;
      margin-right: 0.25em;
      width: 17px;
      height: 17px;
      transition: 0.3s;
      cursor: pointer;
      &:hover {
        background-color: red;
        transform: rotate(180deg);
      }

      & span:nth-child(1n) {
        top: 7px;
        left: 1px;
        transform: rotate(45deg);
      }
      & span:nth-child(2n) {
        top: 7px;
        right: 1px;
        transform: rotate(-45deg);
      }

      .cross {
        position: absolute;
        width: 15px;
        height: 3px;
        background: white;
        transform-origin: center;
        content: " ";
      }
    }
  }

  &_content {
    padding: 0.5em 0.5em 0.5em 0.5em;
  }

  &_blocInput {
    @include flex(row, wrap, space-between, center);

    label {
      @include flex(row, nowrap, center, center);
      @include size(24px, 28px);
      margin: 0 0 0 1.25em;
      font-weight: bold;
      text-align: center;
    }

    input {
      flex: 1;
      max-width: 73px;
      box-sizing: border-box;
      text-align: center;
      &:hover {
        border: 1px solid rgb(255, 136, 0);
        border-radius: $softRadius;
      }

      &:focus {
        border: 1px solid rgb(255, 136, 0);
        border-radius: $softRadius;
      }
    }

    & [type="number"] {
      text-indent: 0.875em;
    }

    svg {
      @include size(1.5em, 1.5em);
    }
  }

  hr {
    margin: 0.5rem 0;
  }
}
